import React from "react"
import styles from "./styles.css"


export const links = () => [{ rel: "stylesheet", href: styles }];


type thumbnailProps = {
    image : string
}

export const Thumbnail = (props : thumbnailProps)=>{

    const {image} = props

    return(
        <div data-testid = "thumbnail">
            <img src={image} alt={image} />
        </div>
    )
}
